import React, { useState } from 'react'
import "../css/signup.css"
import firebase from "../cofig/firebase"
import {  useHistory } from 'react-router-dom'

import {useFormik} from 'formik'
import * as Yup from 'yup'
import { motion } from 'framer-motion'



export default function SignPage() {
    const [isLoading, setIsLoading] = useState(false);
    const history=useHistory()
    const formik =useFormik({
        initialValues:{email:'',password:""},
        onSubmit:(value,formikBag)=>{
         
         setIsLoading(true)
         firebase.auth().createUserWithEmailAndPassword(value.email,value.password).then(res=>{
             history.replace("/")
         }).catch(e=>{
             setIsLoading(false)
formikBag.setFieldError("email",e.message)
         })
            
        },
      validationSchema : Yup.object({
          email:Yup.string().required('email is required').email("email is invalid"),
          password:Yup.string().required('Password is required').min(6)
      })
      
    })


  // if (isLoggedIn) return <Redirect to="/" />;

  return (
  
    <div className="flex h-screen bg-gray-200 signup ">
      <div className="m-auto w-1/3 text-white flex flex-wrap justify-center shadow-lg rounded-lg bg-gradient-to-br from-teal-500 to-pink-400 mobileform">
        <motion.form  animate={{
      scale: [1, 1.5, 1.5, 1, 1],
      rotate: [0, 0, 270, 270, 0]}} className="m-5 w-10/12 " onSubmit={formik.handleSubmit}>
          
          <h1 className="w-full text-4xl tracking-widest text-center my-6">
            Sign Up Here
          </h1>
          <div className="w-full my-6">
            <input
              type="email"
              className="p-2 rounded shadow w-full text-black"
              placeholder="Email or Username"
              name="email"
              value={formik.values.email}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              />
              {formik.touched.email &&  formik.errors.email ?(formik.errors.email):null}
          </div>
          <div className="w-full my-6">
            <input
              type="password"
              className="p-2 rounded shadow w-full text-black"
              placeholder="password"
              name="password"
              value={formik.values.password}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              
            />
          <p className="text-white">{formik.touched.password &&  formik.errors.password ?(formik.errors.password):null}</p> 
          </div>
          <div className="w-full my-10 new">
            <button
              type="submit"
              className="  p-2 rounded shadow w-full bg-gradient-to-r from-pink-500 to-orange-600 hover:from-teal-400 hover:to-blue-500 text-black"
            >
             {isLoading ? (
                <i className="fas fa-circle-notch fa-spin"></i>
              ) : (
                "Signup"
              )}
            </button>
          </div>
        </motion.form>
      </div>
    </div>
   
   
  );
}