import React, { useState } from 'react'
import "../css/Login.css"
import firebase from "../cofig/firebase"
import {  useHistory } from 'react-router-dom'
import { motion } from 'framer-motion'

function LoginPage() {
    const [Loding, setisLoding] = useState(false)
    const [error, seterror] = useState("")
    const [form, setform] = useState({email:"",password:""})
   // const [isLoggedin, setisLoggedin] = useState(false)
    const history =useHistory();

    function handleForm(e){
        if(Loding) return;
        setisLoding(true);
        e.preventDefault();
        firebase.auth().signInWithEmailAndPassword(form.email,form.password).then((res)=>{
            history.replace("/")
            seterror("")
     //       setisLoggedin(true)
            setisLoding(false);
        }).catch(e=>{
            seterror(e.message)
            console.log(e);
            setisLoding(false);
        })
    }
    function handleInput(e){
        setform({...form,[e.target.name]:e.target.value});
    }
   
    return (
        
        <div className="flex h-screen bg-red-300 login">
         <div className="m-auto w-1/3 text-white flex flex-wrap justify-center shadow-lg rounded-lg bg-gradient-to-br from-black-500 to-white-400 mobileform ">
        <motion.form animate={{rotate: 360}} className="m-5 w-10/12 " onSubmit={handleForm}>
           
          <h1 className="w-full text-4xl tracking-widest text-center my-6">
            Login
          </h1>
          <div className="w-full my-6">
            <input
              type="email"
              className="p-2 rounded shadow w-full text-black"
              placeholder="Email or Username"
              name="email"
              value={form.email}
              onChange={handleInput}
            
            />
          </div>
          <div className="w-full my-6">
            <input
              type="password"
              className="p-2 rounded shadow w-full text-black"
              placeholder="password"
              name="password"
              value={form.password}
              onChange={handleInput}
            />
          </div>
          {(error!=="" && <p>{error}</p>)}
          <div className="w-full my-10 ">
            <button
              type="submit"
              className="p-2 rounded shadow w-full bg-gradient-to-r from-pink-500 to-red-300 hover:from-teal-400 hover:to-blue-500 text-black "
        
           >
               {Loding?(<i className=" fas fa-circle-notch fa-spin"></i>):("Login")
               }
              
            </button>
          </div>
        </motion.form>
      </div>
    </div>

    )
}

export default LoginPage
