import React, { useEffect, useState } from "react";
import './css/style.css';
import {BrowserRouter as  Router, Route, Switch} from "react-router-dom"
import HomePage from "./Page/Home";
import LoginPage from "./Page/Login";
import GalleryPage from "./Page/Gallery";
import Headerbar from "./Page/Header";
import firebase from "firebase";
import Appcontext from "./cofig/AppContext";
import SignPage from "./Page/SignUp";
import Gallery2 from "./Page/Newpage";

function App() {

  const [isLoggedIN, setisLoggedIN] = useState(false)
  const [user, setuser] = useState({})
  useEffect(() => {
    firebase.auth().onAuthStateChanged(user=>{
        if(user){
            setisLoggedIN(true)
            setuser(user);
        }
        else{
          setisLoggedIN(false)
          setuser({});

        }
    })
 }, [])
  return (
    <Router>
      <Appcontext.Provider value={[isLoggedIN,user]}>
      <Headerbar></Headerbar>
      <Switch>
      <Route path="/" exact={true}>
        <HomePage></HomePage>
        </Route>
      <Route path="/gallery">
        <GalleryPage></GalleryPage>
        </Route>
        <Route path="/login">
        <LoginPage></LoginPage>
        </Route>
        <Route path="/signup">
       <SignPage></SignPage>
        </Route>
        <Route path="/newpage">
       <Gallery2></Gallery2>
        </Route>
      </Switch>
      </Appcontext.Provider>
    </Router>
  
  );
}

export default App;
