import React, { useContext} from 'react'
import { Link, useHistory } from 'react-router-dom'
import firebase from "firebase"
import Appcontext from '../cofig/AppContext';
import "../css/header.css"
import { Nav, Navbar, NavDropdown } from 'react-bootstrap';


function Headerbar() {
    const history =useHistory();
    const [isLoggedIN,user] = useContext(Appcontext)
  
    function Logout(){
        firebase.auth().signOut().then(res=>{
            history.replace("/login");
        })
    }
    return (
        <div className="stick">
        <nav className=" py-3 bg-pink-400 text-white hover:bg-green-500 ">
          <ul className="flex justify-between px-3">
             <span className="flex">
             <li className="mr-3 text-white">
              <Link className="text-white" to="/"><img src="images/new.jpg" width="30px" height="30px"></img></Link>
            </li>
            <li className="mr-3 text-white">
              <Link className="text-white" to="/">Home</Link>
            </li>
            <li className=" text-white">
            {
                    isLoggedIN?( <Link className="text-white"  to="/gallery">Gallery</Link>):<Link className="text-white"  to="/login">Gallery</Link>
                }
              
            </li>
            </span> 
            <span className="flex">
            <li  className="mr-2">
                {!isLoggedIN&& <Link className="text-white" to="/signup">SignUp</Link>}
             
            </li>
            <li className="ml-3">
                {
                    isLoggedIN?( <button onClick={Logout}>Logout</button>):<Link className="text-white"  to="/login">Login</Link>
                }
               
              
            </li>
          
            </span>
          </ul>
        </nav>
        {/* <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
  <Navbar.Brand href="/"><img src="images/new.jpg" width="30px" height="30px"></img></Navbar.Brand>
  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
  <Navbar.Collapse id="responsive-navbar-nav">
    <Nav className="mr-auto">
      <Nav.Link ><Link className="text-white" to="/">Home </Link> </Nav.Link>
      <Nav.Link>  {
                    isLoggedIN?( <Link className="text-white"  to="/gallery">Gallery</Link>):<Link className="text-white"  to="/login">Gallery</Link>
                }</Nav.Link>
    </Nav>
    <Nav>
      <Nav.Link > {
                    isLoggedIN?( <button onClick={Logout}>Logout</button>):<Link className="text-white"  to="/login">Login</Link>
                } </Nav.Link>
      <Nav.Link eventKey={2} >  {!isLoggedIN&& <Link className="text-white" to="/signup">SignUp</Link>}
      </Nav.Link>
    </Nav>
  </Navbar.Collapse>
</Navbar> */}
        </div>
    )
}

export default Headerbar
